import Vue from 'vue'
import Router from 'vue-router'

import 'html5-reset/assets/css/reset.css'

// 承载
import activityForNightClub from '../pages/activityForNightClubDetail.vue'
// 主页
import activityForNightClubIndex from '../views/activity-for-night-club-detail.vue'

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'activityForNightClub',
    component: activityForNightClubIndex
  }]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#activityForNightClub',
  router,
  components: {
    activityForNightClub
  },
  template: '<activityForNightClub/>'
})



